import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import AllExperimentMarks from "./AllExperimentMarks/AllExperimentMarks";
import AttendanceMarks from "./AttendanceMarks/AttendanceMarks";
import ClsTestMarks from "./ClsTestMarks/ClsTestMarks";
import AssignmentMarks from "./AssignmentMarks/AssignmentMarks";
import TotalMarks from "./TotalMarks/TotalMarks";
import UniversityMarks from "./UniversityMarks/UniversityMarks";
import FinalMarks from "./FinalMarks/FinalMarks";
import UniversitySeat from "./UniversitySeat/UniversitySeat";
import { useContinuousEvaluationDetail } from "../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";

const ContinuousAssesment = ({ subjectId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const { continuousEvaluationDetail, continuousEvaluationDetailRefetch } =
    useContinuousEvaluationDetail({
      data: {
        sid: subjectId,
      },
      skip: !subjectId,
    });
  return (
    <>
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("d"))}
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/member/subject/${params.sname}?a=continuous&n=assesment${tab.urlKey}`
                : `/q/${params.username}/member/subject/${params.sname}?a=continuous&n=assesment${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />

      {searchParams.get("d") === "attendance" ? (
        <AttendanceMarks
          ceid={continuousEvaluationDetail?._id}
          attendance_outof={continuousEvaluationDetail?.attendance_outof}
          attendance_toggle={continuousEvaluationDetail?.attendance_toggle}
          attendance_subject={continuousEvaluationDetail?.attendance_subject}
          attendance_grade_count={
            continuousEvaluationDetail?.attendance_grade_count
          }
          attendance_grade_marks={
            continuousEvaluationDetail?.attendance_grade_marks
          }
          onRefetch={continuousEvaluationDetailRefetch}
          clsId={continuousEvaluationDetail?.class}
        />
      ) : searchParams.get("d") === "test" ? (
        <ClsTestMarks
          ceid={continuousEvaluationDetail?._id}
          cls_test_outof={continuousEvaluationDetail?.cls_test_outof}
          cls_test_toggle={continuousEvaluationDetail?.cls_test_toggle}
          cls_test_subject={continuousEvaluationDetail?.cls_test_subject}
          onRefetch={continuousEvaluationDetailRefetch}
          clsId={continuousEvaluationDetail?.class}
        />
      ) : searchParams.get("d") === "assignment" ? (
        <AssignmentMarks
          ceid={continuousEvaluationDetail?._id}
          assignment_outof={continuousEvaluationDetail?.assignment_outof}
          assignment_toggle={continuousEvaluationDetail?.assignment_toggle}
          assignment_subject={continuousEvaluationDetail?.assignment_subject}
          onRefetch={continuousEvaluationDetailRefetch}
          clsId={continuousEvaluationDetail?.class}
        />
      ) : searchParams.get("d") === "total" ? (
        <TotalMarks
          ceid={continuousEvaluationDetail?._id}
          total_outof={continuousEvaluationDetail?.total_outof}
          onRefetch={continuousEvaluationDetailRefetch}
        />
      ) : searchParams.get("d") === "university" ? (
        <UniversityMarks
          ceid={continuousEvaluationDetail?._id}
          university_outof={continuousEvaluationDetail?.university_outof}
          onRefetch={continuousEvaluationDetailRefetch}
        />
      ) : searchParams.get("d") === "final" ? (
        <FinalMarks
          ceid={continuousEvaluationDetail?._id}
          final_outof={continuousEvaluationDetail?.final_outof}
          onRefetch={continuousEvaluationDetailRefetch}
        />
      ) : searchParams.get("d") === "seat" ? (
        <UniversitySeat ceid={continuousEvaluationDetail?._id} />
      ) : (
        <AllExperimentMarks
          ceid={continuousEvaluationDetail?._id}
          experiment_outof={continuousEvaluationDetail?.experiment_outof}
          experiment_toggle={continuousEvaluationDetail?.experiment_toggle}
          onRefetch={continuousEvaluationDetailRefetch}
        />
      )}
    </>
  );
};

export default ContinuousAssesment;

const tabList = [
  {
    label: "experiment",
    id: "experiment",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "attendance",
    id: "attendance",
    urlKey: "&d=attendance",
    groupIs: ["attendance"],
  },
  {
    label: "cls_test",
    id: "test",
    urlKey: "&d=test",
    groupIs: ["test"],
  },
  {
    label: "assignment",
    id: "assignment",
    urlKey: "&d=assignment",
    groupIs: ["assignment"],
  },
  {
    label: "total_marks",
    id: "total",
    urlKey: "&d=total",
    groupIs: ["total"],
  },
  {
    label: "university",
    id: "university",
    urlKey: "&d=university",
    groupIs: ["university"],
  },
  {
    label: "final_marks",
    id: "final",
    urlKey: "&d=final",
    groupIs: ["final"],
  },
  {
    label: "university_seat",
    id: "seat",
    urlKey: "&d=seat",
    groupIs: ["seat"],
  },
];
