import { useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import FormCalendarInput from "../../../../Ui/Input/FormCalendarInput";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../../Utils/Functions/dateUtility";
import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import {
  useInternalEvaluationSingleStudentTakeTest,
  useInternalEvaluationTakeTest,
} from "../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import FormInput from "../../../../Ui/Input/FormInput";

const TakeTestInternalEvaluationTest = ({
  onClose,
  ietid,
  isFlow,
  studentId,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    mcq_test_date: "",
    mcq_test_from: "",
    mcq_test_to: "",
    mcq_test_duration: "",
  });
  const [errorField, setErrorField] = useState("");
  const [internalEvaluationTakeTest] = useInternalEvaluationTakeTest();
  const [internalEvaluationSingleStudentTakeTest] =
    useInternalEvaluationSingleStudentTakeTest();
  const onDateGrab = (val) => {
    setState((prev) => ({
      ...prev,
      mcq_test_date: slashDateFormat(val),
    }));
  };
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTakeTest = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation)) {
      if (ietid) {
        if (isFlow === "SINGLE") {
          if (studentId) {
            setDisabled((pre) => !pre);
            internalEvaluationSingleStudentTakeTest({
              ietid: ietid,
              bodyParameter: {
                ...state,
                mcq_test_date: ymdSlashDateFormat(state?.mcq_test_date),
                mcq_test_from: timeFormatTable(state.mcq_test_from),
                mcq_test_to: timeFormatTable(state.mcq_test_to),
                studentId: studentId,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
                onClose();
              })
              .catch(() => {
                setDisabled((pre) => !pre);
              });
          }
        } else {
          setDisabled((pre) => !pre);
          internalEvaluationTakeTest({
            ietid: ietid,
            bodyParameter: {
              ...state,
              mcq_test_date: ymdSlashDateFormat(state?.mcq_test_date),
              mcq_test_from: timeFormatTable(state.mcq_test_from),
              mcq_test_to: timeFormatTable(state.mcq_test_to),
            },
          })
            .then(() => {
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch(() => {
              setDisabled((pre) => !pre);
            });
        }
      }
    } else {
      setErrorField(validation);
    }
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>
                {t(
                  isFlow === "SINGLE" ? "single_student_take_test" : "take_test"
                )}
              </h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <FormCalendarInput
            label={t("test_taken_date")}
            placeholder={t("test_taken_date_placeholder")}
            name="calendar"
            value={state.mcq_test_date}
            onChange={onDateGrab}
            errorShow={errorField["mcq_test_date"]}
          />
          <FormInput
            label={t("from")}
            placeholder="00:00"
            name={"mcq_test_from"}
            value={state.mcq_test_from}
            type={"time"}
            onChange={onInputChange}
            errorShow={errorField["mcq_test_from"]}
          />
          <FormInput
            label={t("to")}
            placeholder="00:00"
            name={"mcq_test_to"}
            value={state.mcq_test_to}
            type={"time"}
            onChange={onInputChange}
            errorShow={errorField["mcq_test_to"]}
          />
          <FormInput
            label={t("test_duration")}
            placeholder={t("test_duration_placeholder")}
            name={"mcq_test_duration"}
            value={state.mcq_test_duration}
            type="text"
            onChange={onInputChange}
            errorShow={errorField["mcq_test_duration"]}
          />
          <CreateButton label={t("take")} onAction={onTakeTest} />
        </UserMemberContentWrapper>
        {disabled && <QvipleLoader />}
      </div>
    </Modal>
  );
};

export default TakeTestInternalEvaluationTest;

const timeFormatTable = (vale) => {
  const splitValue = vale?.split(":");
  let meridian = "";
  if (splitValue[0] > 12) {
    meridian = "Pm";
    splitValue[0] -= 12;
  } else if (splitValue[0] < 12) {
    meridian = "Am";
    if (splitValue[0] === 0) {
      splitValue[0] = 12;
    }
  } else {
    meridian = "Pm";
  }
  const hour = splitValue[0] > 9 ? splitValue[0] : `0${+splitValue[0]}`;
  return `${hour}:${splitValue[1]} ${meridian}`;
};
