import { useState } from "react";
import style from "../../../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import { useContinuousEvaluationFinalMarks } from "../../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import FinalMarksCard from "./FinalMarksCard";
import Loading from "../../../../../Loader/Loading";
import EmptyMessage from "../../../../../Utils/EmptyMessage/EmptyMessage";
import FinalMarksSetting from "./FinalMarksSetting";

const FinalMarks = ({ ceid, final_outof, onRefetch }) => {
  const { t } = useTranslation();
  const [searchCopy, setSearchCopy] = useState("");
  const [step, setStep] = useState("");

  const {
    continuousEvaluationFinalMarks,
    continuousEvaluationFinalMarksLoading,
    continuousEvaluationFinalMarksRefetch,
  } = useContinuousEvaluationFinalMarks({
    data: {
      ceid: ceid,
    },
    skip: !ceid,
  });

  const onSearchEvent = (e) => {
    if (
      !continuousEvaluationFinalMarksLoading &&
      continuousEvaluationFinalMarks?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
    }
  };
  const onCloseStep = () => {
    setStep("");
  };

  const onRefetchApi = () => {
    onRefetch();
    continuousEvaluationFinalMarksRefetch();
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>
            {t("outof_marks")} {": "} {final_outof ?? 0}
          </h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  style={{
                    minWidth: "320px",
                  }}
                  onKeyUp={onCallBindSearch}
                  value={searchCopy}
                />
              </div>
            </section>
            <CreateButton
              label="setting"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={() => setStep(1)}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {continuousEvaluationFinalMarks?.map((student) => (
          <FinalMarksCard student={student} key={student?._id} />
        ))}
        {continuousEvaluationFinalMarksLoading ? (
          <Loading />
        ) : searchCopy ? (
          continuousEvaluationFinalMarks?.length === 0 && (
            <EmptyMessage title="No item found related this search." />
          )
        ) : (
          continuousEvaluationFinalMarks?.length === 0 && (
            <EmptyMessage title="No item." />
          )
        )}
      </UserMemberContentWrapper>
      {step === 1 ? (
        <FinalMarksSetting
          onClose={onCloseStep}
          onRefetch={onRefetchApi}
          ceid={ceid}
          menuData={{
            final_outof: final_outof,
          }}
        />
      ) : null}
    </>
  );
};

export default FinalMarks;
