import { useState } from "react";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import AddInternalEvaluation from "./AddInternalEvaluation";
import {
  useRemoveInternalEvaluation,
  useUpdateInternalEvaluation,
} from "../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";

const InternalEvaluationCardMenu = ({ onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [removeInternalEvaluation] = useRemoveInternalEvaluation();
  const [updateInternalEvaluation] = useUpdateInternalEvaluation();

  const onDelete = () => {
    if (menuData?._id) {
      setDisabled((pre) => !pre);
      removeInternalEvaluation({
        ieid: menuData?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val) => {
    if (menuData?._id) {
      setDisabled((pre) => !pre);
      updateInternalEvaluation({
        ieid: menuData?._id,
        bodyParameter: val,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  // console.info("menuData", menuData);
  return edit ? (
    <AddInternalEvaluation
      isEdit
      onClose={onOpenEdit}
      onEdit={onEdit}
      menuData={menuData}
    />
  ) : (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onOpenEdit}>{t("edit")}</h6>
      <h6 onClick={onDelete}>{t("delete")}</h6>
    </ModalMenu>
  );
};

export default InternalEvaluationCardMenu;
