import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { assestsUtilityUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import InternalEvaluationStudent from "../InternalEvaluationStudent/InternalEvaluationStudent";
import { useCallback } from "react";
import InternalEvaluationTest from "./InternalEvaluationTest";

const InternalEvaluationDetail = ({ batchId, subjectId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const onNavigate = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/member/subject/${params.sname}`
        : `/q/${params.username}/member/subject/${params.sname}`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);
  return (
    <>
      <UserMemberContainerWrapper
        customStyle={{
          marginTop: "1rem",
        }}
      >
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "0.2rem",
              }}
            >
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              <h6>{getQuery.state?.evaluate?.name ?? ""}</h6>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={
                getQuery.state?.urlOffSet === "Institute"
                  ? `/q/${params.username}/member/subject/${params.sname}?a=evaluate${tab.urlKey}`
                  : `/q/${params.username}/member/subject/${params.sname}?a=evaluate${tab.urlKey}`
              }
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
        {searchParams.get("n") === "internal-list" ? (
          <InternalEvaluationTest
            ieid={getQuery.state?.evaluate?._id}
            subjectId={subjectId}
          />
        ) : (
          <InternalEvaluationStudent subjectId={subjectId} batchId={batchId} />
        )}
      </UserMemberContainerWrapper>
    </>
  );
};

export default InternalEvaluationDetail;
const tabList = [
  {
    label: "student",
    id: "student",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "evaluation",
    id: "internal-list",
    urlKey: "&n=internal-list",
    groupIs: ["internal-list"],
  },
];
