import { useTranslation } from "react-i18next";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import InternalEvaluationTestCard from "./InternalEvaluationTestCard";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { useGetInternalEvaluationTestList } from "../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import AddInternalEvaluationTest from "./AddInternalEvaluationTest";

const InternalEvaluationTest = ({ ieid, subjectId }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    getInternalEvaluationTestList,
    getInternalEvaluationTestListLoading,
    getInternalEvaluationTestListRefetch,
  } = useGetInternalEvaluationTestList({
    data: {
      ieid: ieid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !ieid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (getInternalEvaluationTestList?.list?.length) {
      let obj = nextPage(getInternalEvaluationTestList?.list?.length);
      setState(() => obj);
    }
  }, [getInternalEvaluationTestList?.list]);
  const onSearchEvent = (e) => {
    if (
      !getInternalEvaluationTestListLoading &&
      getInternalEvaluationTestList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onRefetch = useCallback(() => {
    setPage((prev) => (prev = 1));
    getInternalEvaluationTestListRefetch();
  }, [getInternalEvaluationTestListRefetch]);

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
              />
            </div>
          </section>
          <CreateButton
            label="add"
            customStyle={{
              paddingBlock: "0.4rem",
              margin: "0",
            }}
            onAction={onToggle}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {getInternalEvaluationTestList?.list?.map((e_test, index) =>
          getInternalEvaluationTestList?.list?.length === index + 1 ? (
            <div key={e_test?._id} ref={ref}>
              <InternalEvaluationTestCard
                e_test={e_test}
                onRefetch={onRefetch}
              />
            </div>
          ) : (
            <InternalEvaluationTestCard
              e_test={e_test}
              key={e_test?._id}
              onRefetch={onRefetch}
            />
          )
        )}
        {getInternalEvaluationTestListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
          )
        ) : search ? (
          getInternalEvaluationTestList?.list?.length === 0 && (
            <EmptyMessage title="No item found related this search." />
          )
        ) : (
          getInternalEvaluationTestList?.list?.length === 0 && (
            <EmptyMessage title="No item." />
          )
        )}
      </UserMemberContentWrapper>
      {add && (
        <AddInternalEvaluationTest
          ieid={ieid}
          onClose={onToggle}
          onRefetch={onRefetch}
          subjectId={subjectId}
        />
      )}
    </>
  );
};

export default InternalEvaluationTest;
