import { useState } from "react";
import style from "../../../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import { useContinuousEvaluationAssignmentMarks } from "../../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import AssignmentMarksSetting from "./AssignmentMarksSetting";
import AssignmentMarksCard from "./AssignmentMarksCard";
import Loading from "../../../../../Loader/Loading";
import EmptyMessage from "../../../../../Utils/EmptyMessage/EmptyMessage";

const AssignmentMarks = ({
  ceid,
  assignment_outof,
  assignment_toggle,
  assignment_subject,
  onRefetch,
  clsId,
}) => {
  const { t } = useTranslation();
  const [searchCopy, setSearchCopy] = useState("");
  const [step, setStep] = useState("");

  const {
    continuousEvaluationAssignmentMarks,
    continuousEvaluationAssignmentMarksLoading,
    continuousEvaluationAssignmentMarksRefetch,
  } = useContinuousEvaluationAssignmentMarks({
    data: {
      ceid: ceid,
    },
    skip: !ceid,
  });

  const onSearchEvent = (e) => {
    if (
      !continuousEvaluationAssignmentMarksLoading &&
      continuousEvaluationAssignmentMarks?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
    }
  };

  const onCloseStep = () => {
    setStep("");
  };

  const onRefetchApi = () => {
    onRefetch();
    continuousEvaluationAssignmentMarksRefetch();
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>
            {t("outof_marks")} {": "} {assignment_outof ?? 0}
          </h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  style={{
                    minWidth: "320px",
                  }}
                  onKeyUp={onCallBindSearch}
                  value={searchCopy}
                />
              </div>
            </section>
            <CreateButton
              label="setting"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={() => setStep(1)}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {continuousEvaluationAssignmentMarks?.map((student) => (
          <AssignmentMarksCard student={student} key={student?._id} />
        ))}
        {continuousEvaluationAssignmentMarksLoading ? (
          <Loading />
        ) : searchCopy ? (
          continuousEvaluationAssignmentMarks?.length === 0 && (
            <EmptyMessage title="No item found related this search." />
          )
        ) : (
          continuousEvaluationAssignmentMarks?.length === 0 && (
            <EmptyMessage title="No item." />
          )
        )}
      </UserMemberContentWrapper>

      {step === 1 ? (
        <AssignmentMarksSetting
          onClose={onCloseStep}
          onRefetch={onRefetchApi}
          ceid={ceid}
          menuData={{
            assignment_outof: assignment_outof,
            assignment_toggle: assignment_toggle,
            assignment_subject: assignment_subject,
          }}
          clsId={clsId}
        />
      ) : null}
    </>
  );
};

export default AssignmentMarks;
