import TileWrapper from "../../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../../Ui/Tab/Tile/TileCard";
import style from "../../../FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import { useEffect, useState } from "react";
import { useAddInternalEvaluationTest } from "../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import { useSubjectMcqProfile } from "../../../../../hooks/member_tab/subject-api";
import SetButton from "../../../../Ui/Button/SetButton";
import MasterSubjectTestset from "../../../MasterSubjectTestset/MasterSubjectTestset";
import MasterSubjectTestsetCard from "../../../MasterSubjectTestset/MasterSubjectTestsetCard";
const AddInternalEvaluationTest = ({
  onClose,
  ieid,
  onRefetch,
  isEdit,
  menuData,
  onEdit,
  subjectId,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    name: "",
    out_of: "",
    conversion_rate: "",
    test_type: "Offline",
    mcq_testset: "",
  });
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");
  const [addInternalEvaluationTest] = useAddInternalEvaluationTest();
  const { subjectMcqProfile, subjectMcqProfileRefetch } = useSubjectMcqProfile({
    sid: subjectId,
    skip: !subjectId,
  });
  useEffect(() => {
    if (subjectId) subjectMcqProfileRefetch();
  }, [subjectId, subjectMcqProfileRefetch]);

  useEffect(() => {
    if (isEdit) {
      setState({
        name: menuData?.name,
        out_of: +menuData?.out_of,
        conversion_rate: menuData?.conversion_rate,
        test_type: menuData?.test_type,
        mcq_testset: menuData?.testset ?? "",
      });
    }
  }, [isEdit, menuData]);
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTile = (value) => {
    setState((prev) => ({
      ...prev,
      test_type: value,
    }));
  };

  const onSelectMaster = (val) => {
    setState((prev) => ({
      ...prev,
      mcq_testset: val,
      out_of: val?.testTotalNumber,
    }));
    setStep("");
  };
  const onAddEvaluation = () => {
    let validation = null;
    if (state.test_type === "TEST_SET") {
      validation = requiredField(state, []);
    } else {
      validation = requiredField(state, ["mcq_testset"]);
    }
    if (existingRequiredField(validation)) {
      if (isEdit) {
        onEdit({
          name: state.name,
          conversion_rate: state.conversion_rate,
          test_type: state.test_type,
          out_of: +state.out_of,
          mcq_testset: state.mcq_testset?._id,
        });
      } else {
        if (ieid) {
          setDisabled((pre) => !pre);
          addInternalEvaluationTest({
            ieid: ieid,
            bodyParameter: {
              name: state.name,
              out_of: +state.out_of,
              conversion_rate: state.conversion_rate,
              test_type: state.test_type,
              mcq_testset: state.mcq_testset?._id,
            },
          })
            .then(() => {
              onRefetch();
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch({});
        }
      }
    } else {
      setErrorField(validation);
    }
  };

  // console.info("menuData", menuData, state);
  return (
    <>
      {step === 1 ? (
        <MasterSubjectTestset
          onSelectStaff={onSelectMaster}
          title={t("select_testset")}
          onClose={() => setStep("")}
          // selectedId={state?.subId?._id}
          smid={subjectMcqProfile?.subject?.subjectMasterName}
          cmid={
            subjectMcqProfile?.subject?.class?.masterClassName
              ? subjectMcqProfile?.subject?.class?.masterClassName
              : subjectMcqProfile?.subject?.class_master
          }
          usmid={
            subjectMcqProfile?.subject?.universalSubject?.subjectMasterName
          }
          ucmid={subjectMcqProfile?.subject?.universalClass?.masterClassName}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {" "}
                    {t(
                      isEdit
                        ? "update_internal_evaluation_test"
                        : "add_internal_evaluation_test"
                    )}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("interrnal_evaluation_name_test")}
                placeholder={t("interrnal_evaluation_name__test_placeholder")}
                name={"name"}
                value={state.name}
                type={"text"}
                onChange={onInputChange}
                errorShow={errorField["name"]}
              />
              {isEdit ? null : (
                <TileWrapper label="interrnal_evaluation_test_type">
                  {addType?.map((tile) => (
                    <TileCard
                      key={tile?.key}
                      tile={tile}
                      onTile={onTile}
                      activeTile={state.test_type}
                    />
                  ))}
                </TileWrapper>
              )}
              {state.test_type === "TEST_SET" ? (
                <>
                  {state.mcq_testset ? (
                    <MasterSubjectTestsetCard
                      testset={state.mcq_testset}
                      isShowOnly
                    />
                  ) : (
                    <SetButton
                      label="add_test_set"
                      onAction={() => setStep(1)}
                    />
                  )}
                </>
              ) : (
                <FormInput
                  label={t("interrnal_evaluation_out_of_test")}
                  placeholder={t(
                    "interrnal_evaluation_out_of__test_placeholder"
                  )}
                  name={"out_of"}
                  value={state.out_of}
                  type={"text"}
                  onChange={onInputChange}
                  errorShow={errorField["out_of"]}
                />
              )}

              <FormInput
                label={t("interrnal_evaluation_converstion_rate")}
                placeholder={t(
                  "interrnal_evaluation_converstion_rate_placeholder"
                )}
                name={"conversion_rate"}
                value={state.conversion_rate}
                type={"text"}
                onChange={onInputChange}
                errorShow={errorField["conversion_rate"]}
              />

              <CreateButton
                label={isEdit ? "update" : "add"}
                onAction={onAddEvaluation}
              />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AddInternalEvaluationTest;
const addType = [
  {
    key: "Offline",
    name: "offline",
  },
  {
    key: "TEST_SET",
    name: "mcq",
  },
];
