import { useTranslation } from "react-i18next";
import { useGetClassBatchAllStudent } from "../../../../../hooks/member_tab/class-api";
import { useSubjectAllStudent } from "../../../../../hooks/member_tab/subject-api";
import { useEffect, useState } from "react";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import {
  useGetInternalEvaluationTestStudentList,
  useMarkGiveInternalEvaluationTest,
} from "../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import { useLocation } from "react-router-dom";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import CreateButton from "../../../../Ui/Button/CreateButton";
import InternalEvaluationTestDetailCard from "./InternalEvaluationTestDetailCard";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { Notfication } from "../../../../../validation/Snackbar";
import TakeTestInternalEvaluationTest from "./TakeTestInternalEvaluationTest";
const InternalEvaluationTestDetail = ({ subjectId, batchId, isDepartment }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();

  const [student, setStudent] = useState([]);
  const [searchCopy, setSearchCopy] = useState("");
  const [dataLoading, setDataLoading] = useState(true);
  const [disabled, setDisabled] = useState(false);
  const [add, setAdd] = useState(false);

  const [state, setState] = useState([]);
  const [stateStudent, setStateStudent] = useState([]);
  const [notificationState, setNotificationState] = useState({
    msg: "",
    run: false,
    isSuccess: false,
  });
  const [markGiveInternalEvaluationTest] = useMarkGiveInternalEvaluationTest();

  const { subjectAllStudent } = useSubjectAllStudent({
    data: {
      sid: subjectId,
    },
    skip: batchId ? !batchId : !subjectId,
  });

  const { getClassBatchAllStudent } = useGetClassBatchAllStudent({
    data: {
      bid: batchId,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !batchId,
  });

  const {
    getInternalEvaluationTestStudentList,
    getInternalEvaluationTestStudentListRefetch,
  } = useGetInternalEvaluationTestStudentList({
    data: {
      ietid: getQuery.state?.e_test?._id,
    },
    skip: !getQuery.state?.e_test?._id,
  });
  useEffect(() => {
    if (batchId) {
      setDataLoading(false);
      setStudent(getClassBatchAllStudent?.all_students);
    } else {
      setDataLoading(false);
      setStudent(subjectAllStudent?.students);
    }
  }, [
    batchId,
    subjectAllStudent?.students,
    getClassBatchAllStudent?.all_students,
  ]);

  useEffect(() => {
    if (getInternalEvaluationTestStudentList?.arr_stu?.length > 0) {
      let arr = [];
      let arr1 = [];
      for (let dfg of getInternalEvaluationTestStudentList?.arr_stu) {
        arr.push({
          student: dfg?.student,
          obtain_marks: dfg?.obtain_marks,
        });
        arr1.push(dfg?.student);
      }
      setState(arr);
      setStateStudent(arr1);
    }
  }, [getInternalEvaluationTestStudentList?.arr_stu]);

  const onSearchEvent = (e) => {
    if (
      subjectAllStudent?.students?.length >= 0 ||
      getClassBatchAllStudent?.all_students?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      let ft = [];
      if (batchId) {
        ft = getStudentFilter(
          getClassBatchAllStudent?.all_students,
          searchCopy
        );
      } else {
        ft = getStudentFilter(subjectAllStudent?.students, searchCopy);
      }
      setStudent(ft);
    }
  };

  const onInputClick = (val) => {
    if (isDepartment) {
    } else {
      if (stateStudent?.includes(val)) {
      } else {
        setState((prev) => [
          ...prev,
          {
            student: val,
            obtain_marks: 0,
          },
        ]);
      }
    }
  };
  const onInputChange = (e) => {
    if (isDepartment) {
    } else {
      if (+e.target.value < 0) {
        setNotificationState({
          msg: t("obtain_marks_validation_2"),
          run: true,
        });
      } else if (+e.target.value <= +getQuery.state?.e_test?.out_of) {
      } else {
        setNotificationState({
          msg: `${t("obtain_marks_validation_1")} ${
            getQuery.state?.e_test?.out_of
          }`,
          run: true,
        });
      }

      let ipn = [...state];
      for (let sub of ipn) {
        if (sub.student === e.target.name) {
          sub.obtain_marks = parseInt(e.target.value);
        }
      }
      setState(ipn);
    }
  };
  const onUpdateMarks = () => {
    if (isDepartment) {
    } else {
      let flag = true;
      for (let mark of state) {
        if (mark?.obtain_marks < 0) {
          setNotificationState({
            msg: t("obtain_marks_validation_2"),
            run: true,
          });
          flag = false;
          break;
        } else if (mark?.obtain_marks > +getQuery.state?.e_test?.out_of) {
          setNotificationState({
            msg: `${t("obtain_marks_validation_1")} ${
              getQuery.state?.e_test?.out_of
            }`,
            run: true,
          });
          flag = false;
          break;
        } else {
        }
      }

      if (getQuery.state?.e_test?._id && flag) {
        setDisabled((pre) => !pre);
        markGiveInternalEvaluationTest({
          ietid: getQuery.state?.e_test?._id,
          bodyParameter: {
            student_data: state,
          },
        })
          .then(() => {
            getInternalEvaluationTestStudentListRefetch();
            setDisabled((pre) => !pre);
            setNotificationState({
              msg: t("obtain_marks_validation_3"),
              run: true,
              isSuccess: true,
            });
          })
          .catch({});
      }
    }
  };
  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  //   console.info("safgdsg", state);
  return (
    <>
      <UserMemberContainerWrapper
        customStyle={{
          marginTop: "1rem",
        }}
      >
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{getQuery.state?.e_test?.name ?? ""}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    onKeyUp={onCallBindSearch}
                    value={searchCopy}
                  />
                </div>
              </section>
              {getQuery.state?.e_test?.test_type === "Offline" ? (
                <CreateButton
                  label="update_mark"
                  customStyle={{
                    paddingBlock: "0.4rem",
                    margin: "0",
                  }}
                  onAction={onUpdateMarks}
                />
              ) : (
                <>
                  {getInternalEvaluationTestStudentList?.eva_detail
                    ?.take_test === "1" ? (
                    <CreateButton
                      label="take_test"
                      customStyle={{
                        paddingBlock: "0.4rem",
                        margin: "0",
                      }}
                      onAction={onToggle}
                    />
                  ) : null}
                </>
              )}
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          {student?.map((student) => (
            <InternalEvaluationTestDetailCard
              student={student}
              key={student?._id}
              obtainMark={
                getInternalEvaluationTestStudentList &&
                getInternalEvaluationTestStudentList?.obj_stu &&
                getInternalEvaluationTestStudentList?.obj_stu[student?._id]
              }
              testType={getQuery.state?.e_test?.test_type}
              onInputClick={onInputClick}
              onInputChange={onInputChange}
              ietid={getQuery.state?.e_test?._id}
              isRetake={
                getInternalEvaluationTestStudentList?.eva_detail?.take_test
              }
            />
          ))}
          {dataLoading && <Loading />}
          {searchCopy
            ? student?.length === 0 && (
                <EmptyMessage
                  title="No student found related this search"
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                />
              )
            : batchId
            ? getClassBatchAllStudent?.all_students?.length === 0 && (
                <EmptyMessage
                  title="No student found."
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                />
              )
            : subjectAllStudent?.students?.length === 0 && (
                <EmptyMessage
                  title="No student found."
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                />
              )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
      <Notfication
        msg={notificationState.msg}
        run={notificationState.run}
        setRun={() => setNotificationState({ msg: "", run: false })}
        postiton="bottom"
        type={notificationState.isSuccess ? "success" : "error"}
      />

      {add && (
        <TakeTestInternalEvaluationTest
          onClose={onToggle}
          ietid={getQuery.state?.e_test?._id}
        />
      )}
    </>
  );
};

export default InternalEvaluationTestDetail;
const getStudentFilter = (list, args) => {
  if (list?.length > 0) {
    return list?.filter((student) => {
      let stu = `${student?.studentFirstName} ${
        student?.studentMiddleName ? student?.studentMiddleName + " " : ""
      }${student?.studentLastName}`;
      if (stu?.toLowerCase()?.includes(args?.toLowerCase())) {
        return student;
      } else {
        return null;
      }
    });
  } else {
    return [];
  }
};
