import style from "../../../../FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import { useEffect, useState } from "react";
import { useContinuousEvaluationUpdateSetting } from "../../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import Modal from "../../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../../Ui/Input/FormInput";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../../Loader/QvipleLoader";
import {
  existingRequiredField,
  requiredField,
} from "../../../../../Validation/functions/checkingRequiredField";

import Switch from "@mui/material/Switch";
import ClsTheorySubjectListDropdown from "../../ClsTheorySubjectListDropdown";
const label = { inputProps: { "aria-label": "Switch demo" } };

const ClsTestMarksSetting = ({ onClose, ceid, onRefetch, menuData, clsId }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    cls_test_outof: "",
    cls_test_toggle: false,
    cls_test_subject: "",
  });
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [continuousEvaluationUpdateSetting] =
    useContinuousEvaluationUpdateSetting();

  useEffect(() => {
    if (menuData) {
      setState({
        cls_test_outof: menuData?.cls_test_outof,
        cls_test_toggle: menuData?.cls_test_toggle,
        cls_test_subject: menuData?.cls_test_subject,
      });
    }
  }, [menuData]);

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: +e.target.value,
    }));
  };

  const onToggle = (e) => {
    setState((prev) => ({
      ...prev,
      cls_test_toggle: e.target.checked,
    }));
  };
  const onSubjectSelect = (dt) => {
    setState((prev) => ({
      ...prev,
      cls_test_subject: dt,
    }));
  };
  const onUpdate = () => {
    let validation = requiredField(state, ["cls_test_toggle"]);
    if (existingRequiredField(validation)) {
      if (ceid) {
        setDisabled((pre) => !pre);
        continuousEvaluationUpdateSetting({
          ceid: ceid,
          bodyParameter: {
            ...state,
            cls_test_subject: state.cls_test_subject?._id ?? null,
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6> {t("cls_test_setting")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("cls_test_outof")}
              placeholder={t("cls_test_outof_placeholder")}
              name={"cls_test_outof"}
              value={state.cls_test_outof}
              type={"text"}
              onChange={onInputChange}
              errorShow={errorField["cls_test_outof"]}
            />
            <ClsTheorySubjectListDropdown
              onSelect={onSubjectSelect}
              errorShow={errorField["subject"]}
              defalutValue={state?.cls_test_subject}
              clsId={clsId}
            />

            <div
              style={{
                marginTop: "1rem",
              }}
            >
              <Switch
                {...label}
                checked={state.cls_test_toggle}
                onChange={onToggle}
              />
              <span>{t("cls_test_enable")}</span>
            </div>

            <CreateButton label={"update"} onAction={onUpdate} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default ClsTestMarksSetting;
