import { useEffect, useState } from "react";
import style from "../../../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import {
  useContinuousEvaluationUniversitySeat,
  useContinuousEvaluationUpdateUniversitySeat,
} from "../../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import UniversitySeatCard from "./UniversitySeatCard";
import Loading from "../../../../../Loader/Loading";
import EmptyMessage from "../../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../../Loader/QvipleLoader";

const UniversitySeat = ({ ceid }) => {
  const { t } = useTranslation();
  const [searchCopy, setSearchCopy] = useState("");
  const [state, setState] = useState(null);
  const [disabled, setDisabled] = useState(false);

  const [continuousEvaluationUpdateUniversitySeat] =
    useContinuousEvaluationUpdateUniversitySeat();

  const {
    continuousEvaluationUniversitySeat,
    continuousEvaluationUniversitySeatLoading,
    continuousEvaluationUniversitySeatRefetch,
  } = useContinuousEvaluationUniversitySeat({
    data: {
      ceid: ceid,
    },
    skip: !ceid,
  });

  const onSearchEvent = (e) => {
    if (
      !continuousEvaluationUniversitySeatLoading &&
      continuousEvaluationUniversitySeat?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
    }
  };

  const onRefetchApi = () => {
    continuousEvaluationUniversitySeatRefetch();
  };

  useEffect(() => {
    if (continuousEvaluationUniversitySeat?.length > 0) {
      let obj = {};

      for (let st of continuousEvaluationUniversitySeat) {
        obj[st?._id] = {
          seat_no: st?.seat_no,
        };
      }
      setState(obj);
    }
  }, [continuousEvaluationUniversitySeat?.length]);

  const onInputChange = (e) => {
    let obj = { ...state };
    if (obj[e.target.name]) {
      obj[e.target.name]["seat_no"] = e.target.value;
    }
    setState(obj);
  };

  const onSaveMarks = () => {
    if (ceid) {
      setDisabled((pre) => !pre);
      let seat_list = [];

      for (let obj in state) {
        seat_list.push({
          student: obj,
          seat_no: state[obj]["seat_no"],
        });
      }
      continuousEvaluationUpdateUniversitySeat({
        ceid: ceid,
        bodyParameter: {
          seat_list,
        },
      })
        .then(() => {
          continuousEvaluationUniversitySeatRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
              />
            </div>
          </section>
          <CreateButton
            label="save"
            onAction={onSaveMarks}
            customStyle={{
              margin: "0",
              padding: "0.3rem 0.4rem",
              minWidth: "fit-content",
            }}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {continuousEvaluationUniversitySeat?.map((student) => (
          <UniversitySeatCard
            student={student}
            key={student?._id}
            onInputChange={onInputChange}
            data={state && state[student?._id]}
          />
        ))}
        {continuousEvaluationUniversitySeatLoading ? (
          <Loading />
        ) : searchCopy ? (
          continuousEvaluationUniversitySeat?.length === 0 && (
            <EmptyMessage title="No item found related this search." />
          )
        ) : (
          continuousEvaluationUniversitySeat?.length === 0 && (
            <EmptyMessage title="No item." />
          )
        )}
      </UserMemberContentWrapper>

      {disabled && <QvipleLoader />}
    </>
  );
};

export default UniversitySeat;
