import { useTranslation } from "react-i18next";
import { useGetClassBatchAllStudent } from "../../../../../hooks/member_tab/class-api";
import { useSubjectAllStudent } from "../../../../../hooks/member_tab/subject-api";
import { useEffect, useState } from "react";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import InternalEvaluationStudentCard from "./InternalEvaluationStudentCard";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import {
  useGetInternalEvaluationStudentList,
  useInternalEvaluationExportStudent,
} from "../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import { useLocation } from "react-router-dom";
import { pdfCdnUrl } from "../../../../Services/UrlConfig/BaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
const InternalEvaluationStudent = ({ subjectId, batchId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();

  const [student, setStudent] = useState([]);
  const [searchCopy, setSearchCopy] = useState("");
  const [dataLoading, setDataLoading] = useState(true);
  const [disabled, setDisabled] = useState(false);
  const [internalEvaluationExportStudent] =
    useInternalEvaluationExportStudent();

  const { subjectAllStudent } = useSubjectAllStudent({
    data: {
      sid: subjectId,
    },
    skip: batchId ? !batchId : !subjectId,
  });

  const { getClassBatchAllStudent } = useGetClassBatchAllStudent({
    data: {
      bid: batchId,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !batchId,
  });

  const { getInternalEvaluationStudentList } =
    useGetInternalEvaluationStudentList({
      data: {
        ieid: getQuery.state?.evaluate?._id,
      },
      skip: !getQuery.state?.evaluate?._id,
    });
  useEffect(() => {
    if (batchId) {
      setDataLoading(false);
      setStudent(getClassBatchAllStudent?.all_students);
    } else {
      setDataLoading(false);
      setStudent(subjectAllStudent?.students);
    }
  }, [
    batchId,
    subjectAllStudent?.students,
    getClassBatchAllStudent?.all_students,
  ]);
  const onSearchEvent = (e) => {
    if (
      subjectAllStudent?.students?.length >= 0 ||
      getClassBatchAllStudent?.all_students?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };

  //   console.info(
  //     "getInternalEvaluationStudentList",
  //     getInternalEvaluationStudentList
  //   );

  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      let ft = [];
      if (batchId) {
        ft = getStudentFilter(
          getClassBatchAllStudent?.all_students,
          searchCopy
        );
      } else {
        ft = getStudentFilter(subjectAllStudent?.students, searchCopy);
      }
      setStudent(ft);
    }
  };
  const onExport = () => {
    if (getQuery.state?.evaluate?._id) {
      setDisabled((pre) => !pre);
      internalEvaluationExportStudent({
        ieid: getQuery.state?.evaluate?._id,
      })
        .then((res) => {
          if (res?.data?.excel_key) {
            setDisabled((pre) => !pre);
            const newWindow = window.open(
              `${pdfCdnUrl}/${res?.data?.excel_key}`,
              "_blank",
              "noopener,noreferrer"
            );
            if (newWindow) newWindow.opener = null;
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
              />
            </div>
          </section>
          <CreateButton
            label="export"
            customStyle={{
              paddingBlock: "0.2rem",
              margin: "0",
            }}
            onAction={onExport}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {student?.map((student) => (
          <InternalEvaluationStudentCard
            student={student}
            key={student?._id}
            obtainMark={
              getInternalEvaluationStudentList &&
              getInternalEvaluationStudentList[student?._id]
            }
          />
        ))}
        {dataLoading && <Loading />}
        {searchCopy
          ? student?.length === 0 && (
              <EmptyMessage
                title="No student found related this search"
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          : batchId
          ? getClassBatchAllStudent?.all_students?.length === 0 && (
              <EmptyMessage
                title="No student found."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          : subjectAllStudent?.students?.length === 0 && (
              <EmptyMessage
                title="No student found."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )}
      </UserMemberContentWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default InternalEvaluationStudent;
const getStudentFilter = (list, args) => {
  if (list?.length > 0) {
    return list?.filter((student) => {
      let stu = `${student?.studentFirstName} ${
        student?.studentMiddleName ? student?.studentMiddleName + " " : ""
      }${student?.studentLastName}`;
      if (stu?.toLowerCase()?.includes(args?.toLowerCase())) {
        return student;
      } else {
        return null;
      }
    });
  } else {
    return [];
  }
};
