import { useState } from "react";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import {
  useContinuousEvaluationRemoveExperiment,
  useContinuousEvaluationUpdateExperiment,
} from "../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import AddContinuousExperiment from "./AddContinuousExperiment";

const ContinuousExperimentCardMenu = ({ onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [continuousEvaluationRemoveExperiment] =
    useContinuousEvaluationRemoveExperiment();
  const [continuousEvaluationUpdateExperiment] =
    useContinuousEvaluationUpdateExperiment();

  const onDelete = () => {
    if (menuData?._id) {
      setDisabled((pre) => !pre);
      continuousEvaluationRemoveExperiment({
        exid: menuData?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val) => {
    if (menuData?._id) {
      onOpenEdit();
      setDisabled((pre) => !pre);
      continuousEvaluationUpdateExperiment({
        exid: menuData?._id,
        bodyParameter: val,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return edit ? (
    <AddContinuousExperiment
      isEdit
      onClose={onOpenEdit}
      onEdit={onEdit}
      menuData={menuData}
    />
  ) : (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onOpenEdit}>{t("edit")}</h6>
      <h6 onClick={onDelete}>{t("delete")}</h6>
    </ModalMenu>
  );
};

export default ContinuousExperimentCardMenu;
