import TileWrapper from "../../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../../Ui/Tab/Tile/TileCard";
import style from "../../../FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import { useEffect, useState } from "react";
import { useContinuousEvaluationAddExperiment } from "../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import dayjs from "dayjs";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../../Utils/Functions/dateUtility";
import FormCalendarInput from "../../../../Ui/Input/FormCalendarInput";

const AddContinuousExperiment = ({
  onClose,
  ieid,
  onRefetch,
  isEdit,
  menuData,
  onEdit,
  subjectId,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    name: "",
    experiment_type: "Practical",
    date: "",
  });
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [continuousEvaluationAddExperiment] =
    useContinuousEvaluationAddExperiment();

  useEffect(() => {
    if (isEdit) {
      setState({
        name: menuData?.name,
        experiment_type: menuData?.experiment_type,
        date: menuData?.date ? dayjs(menuData?.date)?.format("DD/MM/YYYY") : "",
      });
    }
  }, [isEdit, menuData]);

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTile = (value) => {
    setState((prev) => ({
      ...prev,
      experiment_type: value,
    }));
  };

  const onDateGrab = (val) => {
    setState((prev) => ({
      ...prev,
      date: slashDateFormat(val),
    }));
  };

  const onAddExperiment = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation)) {
      if (isEdit) {
        onEdit({
          ...state,
          date: ymdSlashDateFormat(state.date),
        });
      } else {
        if (subjectId) {
          setDisabled((pre) => !pre);
          continuousEvaluationAddExperiment({
            sid: subjectId,
            bodyParameter: {
              ...state,
              date: ymdSlashDateFormat(state.date),
            },
          })
            .then(() => {
              onRefetch();
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch({});
        }
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6> {t(isEdit ? "update_experiment" : "add_experiment")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("experiment_name")}
              placeholder={t("experiment_name_placeholder")}
              name={"name"}
              value={state.name}
              type={"text"}
              onChange={onInputChange}
              errorShow={errorField["name"]}
            />
            <TileWrapper
              label="experiment_type"
              errorShow={errorField["experiment_type"]}
            >
              {addType?.map((tile) => (
                <TileCard
                  key={tile?.key}
                  tile={tile}
                  onTile={onTile}
                  activeTile={state.experiment_type}
                />
              ))}
            </TileWrapper>

            <FormCalendarInput
              label={t("date")}
              placeholder={t("date_placeholder")}
              name="calendar"
              value={state.date}
              onChange={onDateGrab}
              errorShow={errorField["date"]}
            />

            <CreateButton
              label={isEdit ? "update" : "add"}
              onAction={onAddExperiment}
            />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AddContinuousExperiment;

const addType = [
  {
    key: "Theory",
    name: "theory",
  },
  {
    key: "Practical",
    name: "practical",
  },
  {
    key: "Tutorial",
    name: "tutorial",
  },
];
