import style from "../../../../FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import { useEffect, useState } from "react";
import { useContinuousEvaluationUpdateSetting } from "../../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import Modal from "../../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../../Ui/Input/FormInput";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../../Loader/QvipleLoader";
import {
  existingRequiredField,
  requiredField,
} from "../../../../../Validation/functions/checkingRequiredField";

import Switch from "@mui/material/Switch";
const label = { inputProps: { "aria-label": "Switch demo" } };

const AllExperimentMarksSetting = ({ onClose, ceid, onRefetch, menuData }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    experiment_outof: "",
    experiment_toggle: false,
  });
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [continuousEvaluationUpdateSetting] =
    useContinuousEvaluationUpdateSetting();

  useEffect(() => {
    if (menuData) {
      setState({
        experiment_outof: menuData?.experiment_outof,
        experiment_toggle: menuData?.experiment_toggle,
      });
    }
  }, [menuData]);

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: +e.target.value,
    }));
  };

  const onToggle = (e) => {
    setState((prev) => ({
      ...prev,
      experiment_toggle: e.target.checked,
    }));
  };

  const onUpdate = () => {
    let validation = requiredField(state, ["experiment_toggle"]);
    if (existingRequiredField(validation)) {
      if (ceid) {
        setDisabled((pre) => !pre);
        continuousEvaluationUpdateSetting({
          ceid: ceid,
          bodyParameter: state,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6> {t("experiment_setting")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("experiment_outof")}
              placeholder={t("experiment_outof_placeholder")}
              name={"experiment_outof"}
              value={state.experiment_outof}
              type={"text"}
              onChange={onInputChange}
              errorShow={errorField["experiment_outof"]}
            />
            <div
              style={{
                marginTop: "1rem",
              }}
            >
              <Switch
                {...label}
                checked={state.experiment_toggle}
                onChange={onToggle}
              />
              <span>{t("experiment_enable")}</span>
            </div>

            <CreateButton label={"update"} onAction={onUpdate} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AllExperimentMarksSetting;
