import { useEffect, useState } from "react";
import style from "../../../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import {
  useContinuousEvaluationUniversityMarks,
  useContinuousEvaluationUpdateUniversityMarks,
} from "../../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import UniversityMarksCard from "./UniversityMarksCard";
import Loading from "../../../../../Loader/Loading";
import EmptyMessage from "../../../../../Utils/EmptyMessage/EmptyMessage";
import UniversityMarksSetting from "./UniversityMarksSetting";
import QvipleLoader from "../../../../../Loader/QvipleLoader";

const UniversityMarks = ({ ceid, university_outof, onRefetch }) => {
  const { t } = useTranslation();
  const [searchCopy, setSearchCopy] = useState("");
  const [step, setStep] = useState("");
  const [state, setState] = useState(null);
  const [disabled, setDisabled] = useState(false);

  const [continuousEvaluationUpdateUniversityMarks] =
    useContinuousEvaluationUpdateUniversityMarks();

  const {
    continuousEvaluationUniversityMarks,
    continuousEvaluationUniversityMarksLoading,
    continuousEvaluationUniversityMarksRefetch,
  } = useContinuousEvaluationUniversityMarks({
    data: {
      ceid: ceid,
    },
    skip: !ceid,
  });

  const onSearchEvent = (e) => {
    if (
      !continuousEvaluationUniversityMarksLoading &&
      continuousEvaluationUniversityMarks?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
    }
  };
  const onCloseStep = () => {
    setStep("");
  };

  const onRefetchApi = () => {
    onRefetch();
    continuousEvaluationUniversityMarksRefetch();
  };

  useEffect(() => {
    if (continuousEvaluationUniversityMarks?.length > 0) {
      let obj = {};

      for (let st of continuousEvaluationUniversityMarks) {
        obj[st?._id] = {
          marks: st?.marks,
        };
      }
      setState(obj);
    }
  }, [continuousEvaluationUniversityMarks?.length]);

  const onInputChange = (e) => {
    let obj = { ...state };
    if (obj[e.target.name]) {
      obj[e.target.name]["marks"] = +e.target.value;
    }
    setState(obj);
  };

  const onSaveMarks = () => {
    if (ceid) {
      setDisabled((pre) => !pre);
      let marks_list = [];

      for (let obj in state) {
        marks_list.push({
          student: obj,
          marks: state[obj]["marks"],
        });
      }
      continuousEvaluationUpdateUniversityMarks({
        ceid: ceid,
        bodyParameter: {
          marks_list,
        },
      })
        .then(() => {
          continuousEvaluationUniversityMarksRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>
            {t("outof_marks")} {": "} {university_outof ?? 0}
          </h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  style={{
                    minWidth: "320px",
                  }}
                  onKeyUp={onCallBindSearch}
                  value={searchCopy}
                />
              </div>
            </section>
            <CreateButton
              label="setting"
              customStyle={{
                padding: "0.3rem 0.4rem",
                margin: "0",
              }}
              onAction={() => setStep(1)}
            />
            <CreateButton
              label="save"
              onAction={onSaveMarks}
              customStyle={{
                margin: "0",
                padding: "0.3rem 0.4rem",
                minWidth: "fit-content",
              }}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {continuousEvaluationUniversityMarks?.map((student) => (
          <UniversityMarksCard
            student={student}
            key={student?._id}
            onInputChange={onInputChange}
            data={state && state[student?._id]}
          />
        ))}
        {continuousEvaluationUniversityMarksLoading ? (
          <Loading />
        ) : searchCopy ? (
          continuousEvaluationUniversityMarks?.length === 0 && (
            <EmptyMessage title="No item found related this search." />
          )
        ) : (
          continuousEvaluationUniversityMarks?.length === 0 && (
            <EmptyMessage title="No item." />
          )
        )}
      </UserMemberContentWrapper>
      {step === 1 ? (
        <UniversityMarksSetting
          onClose={onCloseStep}
          onRefetch={onRefetchApi}
          ceid={ceid}
          menuData={{
            university_outof: university_outof,
          }}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default UniversityMarks;
