import style from "../../../../FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import { useEffect, useState } from "react";
import { useContinuousEvaluationUpdateSetting } from "../../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import Modal from "../../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../../Ui/Input/FormInput";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../../Loader/QvipleLoader";
import {
  existingRequiredField,
  requiredField,
} from "../../../../../Validation/functions/checkingRequiredField";

const TotalMarksSetting = ({ onClose, ceid, onRefetch, menuData }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    total_outof: "",
  });
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [continuousEvaluationUpdateSetting] =
    useContinuousEvaluationUpdateSetting();

  useEffect(() => {
    if (menuData) {
      setState({
        total_outof: menuData?.total_outof,
      });
    }
  }, [menuData]);

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: +e.target.value,
    }));
  };

  const onUpdate = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation)) {
      if (ceid) {
        setDisabled((pre) => !pre);
        continuousEvaluationUpdateSetting({
          ceid: ceid,
          bodyParameter: state,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6> {t("total_setting")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("total_outof")}
              placeholder={t("total_outof_placeholder")}
              name={"total_outof"}
              value={state.total_outof}
              type={"text"}
              onChange={onInputChange}
              errorShow={errorField["total_outof"]}
            />
            <CreateButton label={"update"} onAction={onUpdate} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default TotalMarksSetting;
