import { useEffect, useState } from "react";
import McqCommonHeader from "../McqCommonHeader";
import style from "./AllQuestion.module.css";
import Question from "./Question";
import { useInView } from "react-intersection-observer";
import { useTranslation } from "react-i18next";
import styleWrapper from "../McqWrapper.module.css";
import { nextPage } from "../../../../../../optimized/Utils/Functions/nextPage";
import { useMcqAllQuestionList } from "../../../../../../optimized/Hooks/ApiHooks/SubjectManager/subject-manager-api";
import QvipleLoader from "../../../../../../optimized/Loader/QvipleLoader";
import Loading from "../../../../../../optimized/Loader/Loading";
import EmptyMessage from "../../../../../../optimized/Utils/EmptyMessage/EmptyMessage";
const AllQuestion = ({
  tabHandler,
  topLevelIndex,
  tabEventName,
  backIndex,
  smid,
  cmid,
  usmid,
  ucmid,
  setCreateTestSet,
  createTestSet,
  setTestIndex,
  setSelectedQuestion,
}) => {
  const { t } = useTranslation();
  const [viewQuestionIndex, setViewQuestionIndex] = useState(0);
  const [questionId, setQuestionId] = useState("");

  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { mcqAllQuestionList, mcqAllQuestionListLoading } =
    useMcqAllQuestionList({
      data: {
        smid: smid,
        cmid: cmid,
        page: page,
        limit: 10,
        usmid: usmid ? usmid : "",
        ucmid: ucmid ? ucmid : "",
        search: search,
      },
      skip: !smid && !cmid,
    });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (mcqAllQuestionList?.list?.length) {
      let obj = nextPage(mcqAllQuestionList?.list?.length);
      setState(() => obj);
    }
  }, [mcqAllQuestionList?.list]);

  const onSearchEvent = (e) => {
    if (!mcqAllQuestionListLoading && mcqAllQuestionList?.list?.length >= 0) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  const onQuestionClick = (val) => {
    if (tabEventName === "SEARCH_QUESTION_SET") {
      setCreateTestSet((prevState) => ({
        ...prevState,
        testTotalQuestion: prevState.testTotalQuestion + 1,
        testTotalNumber: prevState.testTotalNumber + val?.questionNumber,
        questions: [...prevState.questions, val?._id],
      }));
      setSelectedQuestion((prev) => [...prev, val]);
      setTestIndex(4);
    } else if (tabEventName === "TEST_SET_LIST") {
      setQuestionId(val?._id);
      setViewQuestionIndex(1);
    } else {
    }
  };

  // console.info("THis is all question ", searchQuery);
  return (
    <>
      {viewQuestionIndex === 0 && (
        <>
          <div style={{ marginBottom: "1.5vw" }}>
            <McqCommonHeader
              name="Questions"
              tabEventName={tabEventName}
              backIndex={backIndex}
              tabHandler={tabHandler}
              topLevelIndex={topLevelIndex}
            />
          </div>

          <div
            className={styleWrapper.search_container}
            style={{ width: "100%" }}
          >
            <img
              alt="search icon for question"
              src="/images/search-dept-icon.svg"
            />
            <input
              type="text"
              placeholder={t("search_question")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </div>

          <div className={style.question_container}>
            {mcqAllQuestionList?.list?.map((question, index) =>
              mcqAllQuestionList?.list?.length === index + 1 ? (
                <div
                  className={
                    createTestSet?.questions?.includes(question?._id)
                      ? `${style.one_question} ${style.one_question_active}`
                      : style.one_question
                  }
                  onClick={() => onQuestionClick(question)}
                  key={question?._id}
                  ref={ref}
                >
                  <div>
                    <h6>
                      {" "}
                      {t("add_question")} {+question?.questionSNO}
                    </h6>
                    <p style={{ wordBreak: "break-all" }}>
                      {question?.questionDescription}
                    </p>
                  </div>
                  <p>
                    {question?.questionNumber > 9
                      ? question?.questionNumber
                      : `0${question?.questionNumber}`}
                  </p>
                </div>
              ) : (
                <div
                  className={
                    createTestSet?.questions?.includes(question?._id)
                      ? `${style.one_question} ${style.one_question_active}`
                      : style.one_question
                  }
                  onClick={() => onQuestionClick(question)}
                  key={question?._id}
                >
                  <div>
                    <h6>
                      {" "}
                      {t("add_question")} {+question?.questionSNO}
                    </h6>
                    <p style={{ wordBreak: "break-all" }}>
                      {question?.questionDescription}
                    </p>
                  </div>
                  <p>
                    {question?.questionNumber > 9
                      ? question?.questionNumber
                      : `0${question?.questionNumber}`}
                  </p>
                </div>
              )
            )}
            {mcqAllQuestionListLoading ? (
              search ? (
                <QvipleLoader />
              ) : (
                <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
              )
            ) : search ? (
              mcqAllQuestionList?.list?.length === 0 && (
                <EmptyMessage title="No question found related this search." />
              )
            ) : (
              mcqAllQuestionList?.list?.length === 0 && (
                <EmptyMessage title="No question." />
              )
            )}
          </div>
        </>
      )}
      {viewQuestionIndex === 1 && (
        <Question
          topLevelIndex={setViewQuestionIndex}
          backIndex={0}
          questionId={questionId}
        />
      )}
    </>
  );
};

export default AllQuestion;
