import { useTranslation } from "react-i18next";
import { Link, useLocation, useParams } from "react-router-dom";
import style from "../../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import InternalEvaluationTestCardMenu from "./InternalEvaluationTestCardMenu";
import { useState } from "react";
const InternalEvaluationTestCard = ({ e_test, onRefetch }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [menuData, setMenuData] = useState("");

  const onClose = () => {
    setMenuData("");
  };
  const onMenu = (e) => {
    e.preventDefault();
    setMenuData(e_test);
  };
  return (
    <>
      <Link
        to={`/q/${params.username}/member/subject/${params.sname}?a=detail-evaluate`}
        state={{
          ...getQuery.state,
          e_test: e_test,
        }}
      >
        <div className={style.sf_card}>
          <div className={style.sf_card_contatiner}>
            <div className={style.sf_card_contatiner_inner}>
              <h6>
                {t("name")}
                {": "}
                {e_test?.name ?? ""}
              </h6>
              <p>
                {t("test_type")} {": "}{" "}
                {e_test?.test_type === "TEST_SET" ? "MCQ" : e_test?.test_type}
              </p>
              {e_test?.testset?.testName ? (
                <p>
                  {t("testset")} {": "} {e_test?.testset?.testName}
                </p>
              ) : null}

              <p>
                {t("out_of")} {": "} {e_test?.out_of ?? ""}
              </p>
              <p>
                {t("conversion_rate")} {": "} {e_test?.conversion_rate ?? ""}
              </p>
            </div>
          </div>
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onMenu}
          />
        </div>
      </Link>
      {menuData && (
        <InternalEvaluationTestCardMenu
          onClose={onClose}
          onRefetch={onRefetch}
          menuData={menuData}
        />
      )}
    </>
  );
};

export default InternalEvaluationTestCard;
