import { useTranslation } from "react-i18next";
import { useContinuousEvaluationClsSubjectList } from "../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import CustomSelectSubject from "../../../Ui/Select/CustomSelectSubject";
const ClsTheorySubjectListDropdown = ({
  clsId,
  onSelect,
  errorShow,
  defalutValue,
  isDenied,
  customStyleContainer,
}) => {
  const { t } = useTranslation();

  const { continuousEvaluationClsSubjectList } =
    useContinuousEvaluationClsSubjectList({
      data: {
        cid: clsId,
      },
      skip: !clsId,
    });

  return (
    <>
      <CustomSelectSubject
        selectLabel={t("select_subject")}
        selectedValue={t("please_select_subject")}
        options={
          continuousEvaluationClsSubjectList?.length > 0
            ? continuousEvaluationClsSubjectList
            : []
        }
        onClick={onSelect}
        isFull
        errorShow={errorShow}
        defalutValue={defalutValue}
        isDenied={isDenied}
        customStyleContainer={customStyleContainer}
      />
    </>
  );
};

export default ClsTheorySubjectListDropdown;
