import { useTranslation } from "react-i18next";
import { assestsUserAdmissionUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import style from "../PlateformUser/PlateformUser.module.css";
const MasterSubjectTestsetCard = ({
  testset,
  selectedId,
  onSelect,
  selectList,
  isMultiSelect,
  onMultiSelect,
  isShowOnly,
}) => {
  const { t } = useTranslation();
  const onSelectStaff = () => {
    onSelect(testset);
  };
  const onSelectMultipleStaff = () => {
    onMultiSelect(testset);
  };
  return (
    <>
      {isMultiSelect ? (
        <div
          className={style.assign_show_list_each}
          onClick={onSelectMultipleStaff}
          style={{
            border: "0.5px solid #cacaca",
            marginBlock: "0.4rem",
            borderRadius: "6px",
            padding: "0.8rem 1rem",
          }}
        >
          {selectList?.includes(testset?._id) ? (
            <img
              src={`${assestsUserAdmissionUrl}/check.svg`}
              alt="icon"
              style={{
                height: "1.3rem",
                borderRadius: "0",
                cursor: "pointer",
              }}
            />
          ) : (
            <img
              style={{
                height: "1.3rem",
                borderRadius: "0",
                cursor: "pointer",
              }}
              src={`${assestsUserAdmissionUrl}/uncheck.svg`}
              alt="icon"
            />
          )}
          <div className={style.assign_each_paragraph}>
            <h6>{testset?.testName ?? ""}</h6>
            <p>
              {t("questions")} : {testset?.testTotalQuestion}
            </p>
            <p>
              {t("marks")} :{" "}
              {testset?.testTotalNumber > 9
                ? testset?.testTotalNumber
                : `0${testset?.testTotalNumber}`}
            </p>
          </div>
        </div>
      ) : isShowOnly ? (
        <div
          className={style.assign_show_list_each}
          style={{
            border: "0.5px solid #cacaca",
            marginBlock: "0.4rem",
            borderRadius: "6px",
            padding: "0.8rem 1rem",
          }}
        >
          <div className={style.assign_each_paragraph}>
            <h6>{testset?.testName ?? ""}</h6>
            <p>
              {t("questions")} : {testset?.testTotalQuestion}
            </p>
            <p>
              {t("marks")} :{" "}
              {testset?.testTotalNumber > 9
                ? testset?.testTotalNumber
                : `0${testset?.testTotalNumber}`}
            </p>
          </div>
        </div>
      ) : (
        <div
          className={style.assign_show_list_each}
          style={
            selectedId === testset?._id
              ? {
                  backgroundColor: "rgba(75, 26, 133, 0.1)",
                  border: "0.5px solid #cacaca",
                  marginBlock: "0.4rem",
                  borderRadius: "6px",
                  padding: "0.8rem 1rem",
                }
              : {
                  border: "0.5px solid #cacaca",
                  marginBlock: "0.4rem",
                  borderRadius: "6px",
                  padding: "0.8rem 1rem",
                }
          }
          onClick={onSelectStaff}
        >
          <div className={style.assign_each_paragraph}>
            <h6>{testset?.testName ?? ""}</h6>
            <p>
              {t("questions")} : {testset?.testTotalQuestion}
            </p>
            <p>
              {t("marks")} :{" "}
              {testset?.testTotalNumber > 9
                ? testset?.testTotalNumber
                : `0${testset?.testTotalNumber}`}
            </p>
          </div>
        </div>
      )}
    </>
  );
};

export default MasterSubjectTestsetCard;
