import { useState } from "react";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import AddInternalEvaluationTest from "./AddInternalEvaluationTest";
import {
  useRemoveInternalEvaluationTest,
  useUpdateInternalEvaluationTest,
} from "../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";

const InternalEvaluationTestCardMenu = ({ onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [removeInternalEvaluationTest] = useRemoveInternalEvaluationTest();
  const [updateInternalEvaluationTest] = useUpdateInternalEvaluationTest();

  const onDelete = () => {
    if (menuData?._id) {
      setDisabled((pre) => !pre);
      removeInternalEvaluationTest({
        ietid: menuData?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val) => {
    if (menuData?._id) {
      setDisabled((pre) => !pre);
      updateInternalEvaluationTest({
        ietid: menuData?._id,
        bodyParameter: val,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return edit ? (
    <AddInternalEvaluationTest
      isEdit
      onClose={onOpenEdit}
      onEdit={onEdit}
      menuData={menuData}
    />
  ) : (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onOpenEdit}>{t("edit")}</h6>
      <h6 onClick={onDelete}>{t("delete")}</h6>
    </ModalMenu>
  );
};

export default InternalEvaluationTestCardMenu;
