import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import InternalEvaluationCard from "./InternalEvaluationCard";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { useGetInternalEvaluationList } from "../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import AddInternalEvaluation from "./AddInternalEvaluation";

const InternalEvaluation = ({ subjectId }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    getInternalEvaluationList,
    getInternalEvaluationListLoading,
    getInternalEvaluationListRefetch,
  } = useGetInternalEvaluationList({
    data: {
      sid: subjectId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !subjectId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (getInternalEvaluationList?.list?.length) {
      let obj = nextPage(getInternalEvaluationList?.list?.length);
      setState(() => obj);
    }
  }, [getInternalEvaluationList?.list]);
  const onSearchEvent = (e) => {
    if (
      !getInternalEvaluationListLoading &&
      getInternalEvaluationList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onRefetch = useCallback(() => {
    setPage((prev) => (prev = 1));
    getInternalEvaluationListRefetch();
  }, [getInternalEvaluationListRefetch]);

  return (
    <>
      <UserMemberContainerWrapper
        customStyle={{
          marginTop: "1rem",
        }}
      >
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("internal_evaluation")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    onKeyUp={onCallBindSearch}
                    value={searchCopy}
                  />
                </div>
              </section>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          {getInternalEvaluationList?.list?.map((evaluate, index) =>
            getInternalEvaluationList?.list?.length === index + 1 ? (
              <div key={evaluate?._id} ref={ref}>
                <InternalEvaluationCard
                  evaluate={evaluate}
                  onRefetch={onRefetch}
                />
              </div>
            ) : (
              <InternalEvaluationCard
                evaluate={evaluate}
                key={evaluate?._id}
                onRefetch={onRefetch}
              />
            )
          )}
          {getInternalEvaluationListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            getInternalEvaluationList?.list?.length === 0 && (
              <EmptyMessage title="No item found related this search." />
            )
          ) : (
            getInternalEvaluationList?.list?.length === 0 && (
              <EmptyMessage title="No item." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <AddInternalEvaluation
          sid={subjectId}
          onClose={onToggle}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default InternalEvaluation;
