import { useState } from "react";
import AllQuestion from "../Question/AllQuestion";
import SearchQuestion from "../SearchQuestion";
import style from "./CreateSet.module.css";
import SetList from "./SetList";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../../../optimized/Ui/Button/CreateButton";
import { useMqcQuestionExcelImport } from "../../../../../../optimized/Hooks/ApiHooks/SubjectManager/subject-manager-api";
import ExcelImport from "../../../../../../optimized/ExcelImport/ExcelImport";
import QvipleLoader from "../../../../../../optimized/Loader/QvipleLoader";
import { mcqQuestionRequiredFormat } from "../../../../../../optimized/Constant/Excel/subject_excel";

const QuestionAndSet = ({
  setTestIndex,
  createTestSet,
  smid,
  cmid,
  usmid,
  ucmid,
  setCreateTestSet,
  setSelectedQuestion,
  setTestSetId,
}) => {
  const [showIndex, setShowIndex] = useState(0);
  const showHandler = () => {};
  const { t } = useTranslation();

  const [excel, setExcel] = useState(false);
  const [disabled, setDisabled] = useState(false);

  const [mqcQuestionExcelImport] = useMqcQuestionExcelImport();
  const onExcelUpload = () => {
    setExcel((pre) => !pre);
  };

  const onImportFileInSystem = (list, count) => {
    if (list?.length > 0 && count && smid && cmid) {
      onExcelUpload();
      setDisabled(true);
      mqcQuestionExcelImport({
        smid: smid,
        cmid: cmid,
        flow: "TEST_SET",
        bodyParameter: {
          excel_arr: list,
          excel_count: count,
        },
      })
        .then((res) => {
          if (res?.data?.testset_question?.length > 0) {
            let arrId = [];
            let arr = [];
            let t_total = 0;
            for (let ft of res?.data?.testset_question) {
              arrId.push(ft?._id);
              arr.push(ft);
              t_total += ft?.questionNumber;
            }
            setCreateTestSet((prevState) => ({
              ...prevState,
              testTotalQuestion:
                prevState.testTotalQuestion +
                res?.data?.testset_question?.length,
              testTotalNumber: prevState.testTotalNumber + t_total,
              questions: [...prevState.questions, ...arrId],
            }));
            setSelectedQuestion((prev) => [...prev, ...arr]);
            setDisabled(false);
            setExcel(false);
            setTestIndex(4);
          }
        })
        .catch({});
    }
  };

  return (
    <>
      {showIndex === 0 && (
        <>
          <div
            className={style.create_set_header}
            style={{ marginBottom: "1vw" }}
          >
            <div className={style.left_set_header}>
              <img
                src="/images/members/arrow-left.svg"
                alt=""
                onClick={() => {
                  setTestIndex(1);
                }}
              />
              <h6>{createTestSet?.testName || t("test_set_name")}</h6>
            </div>
            <div className={style.left_set_header}>
              <SearchQuestion
                width="250px"
                tabEventName="SEARCH_QUESTION"
                tabHandler={showHandler}
                backIndex={1}
                topLevelIndex={setShowIndex}
              />
              <CreateButton
                label="import_question"
                onAction={onExcelUpload}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 0.5rem",
                }}
              />
              <div
                className={style.add_question}
                style={{ marginTop: "0" }}
                onClick={() => setTestIndex(3)}
              >
                {t("add_new_question")}
              </div>
            </div>

            {/* <img
            className={style.createMCQQuestionTopMenu}
            src="/images/members/edit-icon.svg"
            alt=""
          /> */}
          </div>

          <SetList
            tabHandler={showHandler}
            setTestIndex={setTestIndex}
            backIndex={5}
            tabEventName="TEST_SET_OPEN"
            smid={smid}
            cmid={cmid}
            usmid={usmid}
            ucmid={ucmid}
            setTestSetId={setTestSetId}
          />
        </>
      )}
      {showIndex === 1 && (
        <AllQuestion
          tabHandler={showHandler}
          topLevelIndex={setShowIndex}
          setTestIndex={setTestIndex}
          tabEventName="SEARCH_QUESTION_SET"
          backIndex={0}
          smid={smid}
          cmid={cmid}
          usmid={usmid}
          ucmid={ucmid}
          setCreateTestSet={setCreateTestSet}
          createTestSet={createTestSet}
          setSelectedQuestion={setSelectedQuestion}
        />
      )}
      {excel && (
        <ExcelImport
          onClose={onExcelUpload}
          onImportFileInSystem={onImportFileInSystem}
          requiredFormat={mcqQuestionRequiredFormat}
        />
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default QuestionAndSet;
