import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useState } from "react";
import TakeTestInternalEvaluationTest from "./TakeTestInternalEvaluationTest";
const InternalEvaluationTestDetailCard = ({
  student,
  obtainMark,
  testType,
  onInputChange,
  onInputClick,
  ietid,
  isRetake,
}) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <div
        className={style.sf_card}
        style={{
          border: "none",
          borderBottom: "0.5px solid #cacaca",
          padding: "0.6rem 1rem",
          marginTop: "0",
        }}
      >
        <div className={style.sf_card_contatiner}>
          <img
            alt="Student Avatar"
            src={
              student?.studentProfilePhoto
                ? `${imageShowUrl}/${student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            style={{
              width: "2.5rem",
              height: "2.5rem",
            }}
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>{`${student?.studentFirstName} ${
              student?.studentMiddleName ? student?.studentMiddleName + " " : ""
            }${student?.studentLastName}`}</h6>
            <p>
              {t("roll_number")} {": "} {student?.studentROLLNO ?? ""}
            </p>
          </div>
        </div>
        <div className={style.sf_card_contatiner}>
          {testType === "Offline" ? (
            <input
              type="tel"
              name={student?._id}
              defaultValue={obtainMark === 0 ? "" : obtainMark}
              placeholder="0"
              onClick={(e) => onInputClick(student?._id, e)}
              onChange={onInputChange}
              className={style.sfcc_input}
            />
          ) : (
            <>
              <h6>{obtainMark ?? 0}</h6>
              {isRetake === "1" ? (
                <CreateButton
                  label="take_test"
                  customStyle={{
                    paddingBlock: "0.4rem",
                    margin: "0",
                  }}
                  onAction={onToggle}
                />
              ) : (
                <CreateButton
                  label="re_take_test"
                  customStyle={{
                    paddingBlock: "0.4rem",
                    margin: "0",
                  }}
                  onAction={onToggle}
                />
              )}
            </>
          )}
        </div>
      </div>
      {add && (
        <TakeTestInternalEvaluationTest
          onClose={onToggle}
          ietid={ietid}
          isFlow="SINGLE"
          studentId={student?._id}
        />
      )}
    </>
  );
};

export default InternalEvaluationTestDetailCard;
