import { useState } from "react";
import { useSubjectMcqTestsetTaken } from "../../../../../../hooks/member_tab/subject-api";
import QLoader from "../../../../../../Loader/QLoader";
import McqCommonHeader from "../McqCommonHeader";
import style from "./CreateSet.module.css";
import { useTranslation } from "react-i18next";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../../../../optimized/Utils/Functions/dateUtility";
import {
  existingRequiredField,
  requiredField,
} from "../../../../../../optimized/Validation/functions/checkingRequiredField";
import FormInput from "../../../../../../optimized/Ui/Input/FormInput";
import FormCalendarInput from "../../../../../../optimized/Ui/Input/FormCalendarInput";
import CreateButton from "../../../../../../optimized/Ui/Button/CreateButton";

import Switch from "@mui/material/Switch";
const label = { inputProps: { "aria-label": "Switch demo" } };

function TakeTestForm({
  setTakeTestIndex,
  tsid,
  sid,
  tabHandler,
  setTestIndex,
}) {
  const { t } = useTranslation();

  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    testExamName: "",
    testDate: "",
    testStart: "",
    testEnd: "",
    testDuration: "",
    is_shuffle_question: false,
  });
  const [errorField, setErrorField] = useState("");

  const [subjectMcqTestsetTaken] = useSubjectMcqTestsetTaken();

  const onDateGrab = (val) => {
    setState((prev) => ({
      ...prev,
      testDate: slashDateFormat(val),
    }));
  };

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onShuffle = (e) => {
    setState((prev) => ({
      ...prev,
      is_shuffle_question: e.target.checked,
    }));
  };
  const onTakeTest = () => {
    let validation = requiredField(state, ["is_shuffle_question"]);
    if (existingRequiredField(validation) && sid && tsid) {
      setDisabled((pre) => !pre);
      subjectMcqTestsetTaken({
        sid: sid,
        takeTestset: {
          tsid: tsid,
          ...state,
          testDate: ymdSlashDateFormat(state?.testDate),
          testStart: timeFormatTable(state.testStart),
          testEnd: timeFormatTable(state.testEnd),
        },
      })
        .then(() => {
          tabHandler("TEST_SET_LIST");
          setTestIndex("3");
          setDisabled((pre) => !pre);
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <McqCommonHeader
        name="Test Set"
        tabEventName="TEST_SET_VIEW"
        backIndex={0}
        tabHandler={() => {}}
        topLevelIndex={setTakeTestIndex}
      />
      <div className={style.form_container}>
        <FormInput
          label={t("exam/test_name")}
          placeholder={t("enter_the_exam_name")}
          name={"testExamName"}
          value={state.testExamName}
          type={"text"}
          onChange={onInputChange}
          errorShow={errorField["testExamName"]}
        />
        <FormCalendarInput
          label={t("date")}
          placeholder={t("date_placeholder")}
          name="calendar"
          value={state.testDate}
          onChange={onDateGrab}
          errorShow={errorField["testDate"]}
        />
        <FormInput
          label={t("from")}
          placeholder="00:00"
          name={"testStart"}
          value={state.testStart}
          type={"time"}
          onChange={onInputChange}
          errorShow={errorField["testStart"]}
        />
        <FormInput
          label={t("to")}
          placeholder="00:00"
          name={"testEnd"}
          value={state.testEnd}
          type={"time"}
          onChange={onInputChange}
          errorShow={errorField["testEnd"]}
        />
        <FormInput
          label={t("test_duration")}
          placeholder={t("test_duration_placeholder")}
          name={"testDuration"}
          value={state.testDuration}
          type="text"
          onChange={onInputChange}
          errorShow={errorField["testDuration"]}
        />
        <div
          style={{
            marginTop: "0.7rem",
          }}
        >
          <Switch
            {...label}
            checked={state.is_shuffle_question}
            onChange={onShuffle}
          />
          <span>{t("question_shuffle")}</span>
        </div>
        <CreateButton label={t("take_test")} onAction={onTakeTest} />
      </div>
      {disabled && <QLoader />}
    </>
  );
}

export default TakeTestForm;
const timeFormatTable = (vale) => {
  const splitValue = vale?.split(":");
  let meridian = "";
  if (splitValue[0] > 12) {
    meridian = "Pm";
    splitValue[0] -= 12;
  } else if (splitValue[0] < 12) {
    meridian = "Am";
    if (splitValue[0] === 0) {
      splitValue[0] = 12;
    }
  } else {
    meridian = "Pm";
  }
  const hour = splitValue[0] > 9 ? splitValue[0] : `0${+splitValue[0]}`;
  return `${hour}:${splitValue[1]} ${meridian}`;
};
