import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormInput from "../../../Ui/Input/FormInput";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Income/Income.module.css";
import Modal from "../../../Ui/Modal/Modal";
import QvipleLoader from "../../../Loader/QvipleLoader";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import { useAddInternalEvaluation } from "../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
const AddInternalEvaluation = ({
  onClose,
  sid,
  onRefetch,
  isEdit,
  menuData,
  onEdit,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    name: "",
    out_of: "",
  });
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [addInternalEvaluation] = useAddInternalEvaluation();
  useEffect(() => {
    if (isEdit) {
      setState({
        name: menuData?.name,
        out_of: +menuData?.out_of,
      });
    }
  }, [isEdit, menuData]);
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onAddEvaluation = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation)) {
      if (isEdit) {
        onEdit({
          name: state.name,
          out_of: +state.out_of,
        });
      } else {
        setDisabled((pre) => !pre);
        addInternalEvaluation({
          sid: sid,
          bodyParameter: {
            name: state.name,
            out_of: +state.out_of,
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>
                  {" "}
                  {t(
                    isEdit
                      ? "update_internal_evaluation"
                      : "add_internal_evaluation"
                  )}
                </h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("interrnal_evaluation_name")}
              placeholder={t("interrnal_evaluation_name_placeholder")}
              name={"name"}
              value={state.name}
              type={"text"}
              onChange={onInputChange}
              errorShow={errorField["name"]}
            />
            <FormInput
              label={t("interrnal_evaluation_out_of")}
              placeholder={t("interrnal_evaluation_out_of_placeholder")}
              name={"out_of"}
              value={state.out_of}
              type={"text"}
              onChange={onInputChange}
              errorShow={errorField["out_of"]}
            />
            <CreateButton
              label={isEdit ? "update" : "add"}
              onAction={onAddEvaluation}
            />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AddInternalEvaluation;
