import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import style from "../../../FinanceManager/FinanceManager.module.css";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import {
  useContinuousOneExperimentDetail,
  useContinuousOneExperimentUpdateMarks,
} from "../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import { useLocation } from "react-router-dom";
import ContinuousExperimentDetailCard from "./ContinuousExperimentDetailCard";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useEffect, useState } from "react";
import QvipleLoader from "../../../../Loader/QvipleLoader";

const ContinuousExperimentDetail = () => {
  const getQuery = useLocation();
  const [state, setState] = useState(null);
  const [disabled, setDisabled] = useState(false);
  const [continuousOneExperimentUpdateMarks] =
    useContinuousOneExperimentUpdateMarks();
  const {
    continuousOneExperimentDetail,
    continuousOneExperimentDetailLoading,
    continuousOneExperimentDetailRefetch,
  } = useContinuousOneExperimentDetail({
    data: {
      exid: getQuery.state?.experiment?._id,
    },
    skip: !getQuery.state?.experiment?._id,
  });

  useEffect(() => {
    if (continuousOneExperimentDetail?.length > 0) {
      let obj = {};

      for (let st of continuousOneExperimentDetail) {
        obj[st?._id] = {
          attendance_marks: st?.attendance_marks,
          practical_marks: st?.practical_marks,
          journal_marks: st?.journal_marks,
          total_marks: st?.total_marks,
        };
      }
      setState(obj);
    }
  }, [continuousOneExperimentDetail]);

  const onInputChange = (e, flow) => {
    let obj = { ...state };
    if (obj[e.target.name]) {
      obj[e.target.name][flow] = +e.target.value;
      obj[e.target.name]["total_marks"] =
        obj[e.target.name]["attendance_marks"] +
        obj[e.target.name]["practical_marks"] +
        obj[e.target.name]["journal_marks"];
    }
    setState(obj);
  };

  const onSaveMarks = () => {
    if (getQuery.state?.experiment?._id) {
      setDisabled((pre) => !pre);
      let marks_list = [];

      for (let obj in state) {
        marks_list.push({
          student: obj,
          attendance_marks: state[obj]["attendance_marks"],
          practical_marks: state[obj]["practical_marks"],
          journal_marks: state[obj]["journal_marks"],
          total_marks: state[obj]["total_marks"],
        });
      }
      continuousOneExperimentUpdateMarks({
        exid: getQuery.state?.experiment?._id,
        bodyParameter: {
          marks_list,
        },
      })
        .then(() => {
          continuousOneExperimentDetailRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContainerWrapper
        customStyle={{
          marginTop: "1rem",
        }}
      >
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{getQuery.state?.experiment?.name ?? ""}</h6>
            <CreateButton
              label="save"
              onAction={onSaveMarks}
              customStyle={{
                margin: "0",
                padding: "0.3rem 1rem",
                minWidth: "fit-content",
              }}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          {continuousOneExperimentDetail?.map((student) => (
            <ContinuousExperimentDetailCard
              student={student}
              key={student?._id}
              onInputChange={onInputChange}
              data={state && state[student?._id]}
            />
          ))}

          {continuousOneExperimentDetailLoading ? (
            <Loading />
          ) : (
            continuousOneExperimentDetail?.list?.length === 0 && (
              <EmptyMessage title="No students." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default ContinuousExperimentDetail;
