import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../UiInput.module.css";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
const CustomSelectSubject = ({
  selectLabel,
  onClick,
  selectedValue,
  options,
  customStyleContainer,
  customLabelStyle,
  customListStyle,
  errorShow,
  defalutValue,
  isFull,
  isDenied,
}) => {
  const { t } = useTranslation();
  const [openOption, setOpenOption] = useState(false);
  const [value, setValue] = useState("");
  const onSelectFunction = () => {
    if (!isDenied) setOpenOption((pre) => !pre);
  };

  useEffect(() => {
    setValue(defalutValue);
  }, [defalutValue]);

  const onChooseOption = (val) => {
    if (isFull) {
      onClick(val);
    } else {
      onClick(val?._id);
    }
    onSelectFunction();
    setValue(val?.subject_group_name ?? val);
  };
  return (
    <div
      className={style.form_input_container}
      style={{ ...customStyleContainer }}
    >
      <p
        className={style.join_form_select_paragraph}
        style={{ ...customLabelStyle }}
      >
        {selectLabel}
        {errorShow && (
          <span style={{ color: "red", marginLeft: "15px" }}>
            * {t("form_require_label")}
          </span>
        )}
      </p>
      <section
        className={style.join_form_select_custom}
        onClick={onSelectFunction}
      >
        {!value ? (
          <p className={style.join_form_select_custom_paragraph}>
            {selectedValue}
          </p>
        ) : (
          <p className={style.join_form_select_custom_paragraph}>
            {value?.selected_batch_query?.batchName ? (
              <>
                ({value?.subject_category === "Practical" ? "P:" : "T:"}
                {value?.selected_batch_query?.batchName}){" "}
              </>
            ) : null}
            {value?.subjectName ?? ""} {value?.class?.classTitle ?? ""}
            {value?.class?.batch?.batchName ? (
              <span
                style={{
                  fontWeight: "400",
                  fontSize: "12px",
                }}
              >
                {" - "}
                {value?.class?.batch?.batchName ?? ""}
              </span>
            ) : null}
          </p>
        )}

        {openOption ? (
          <img
            className={style.join_form_select_custom_rotate_icon}
            src={`${assestsUserFinanceUrl}/down.svg`}
            alt="categor icon"
          />
        ) : (
          <img
            className={style.join_form_select_custom_icon}
            src={`${assestsUserFinanceUrl}/down.svg`}
            alt="categor icon"
          />
        )}
      </section>
      {openOption && (
        <>
          <section
            className={style.join_form_select_custom_list}
            style={customListStyle}
          >
            {options?.map((opt, index) => (
              <p
                key={index}
                onClick={() => onChooseOption(opt)}
                className={style.join_form_select_custom_paragraph_option}
              >
                {opt?.selected_batch_query?.batchName ? (
                  <>
                    ({opt?.subject_category === "Practical" ? "P:" : "T:"}
                    {opt?.selected_batch_query?.batchName}){" "}
                  </>
                ) : null}
                {opt?.subjectName ?? ""} {opt?.class?.classTitle ?? ""}
                {opt?.class?.batch?.batchName ? (
                  <span
                    style={{
                      fontWeight: "400",
                      fontSize: "12px",
                    }}
                  >
                    {" - "}
                    {opt?.class?.batch?.batchName ?? ""}
                  </span>
                ) : null}
              </p>
            ))}
          </section>
        </>
      )}
    </div>
  );
};

export default CustomSelectSubject;
