import style from "../../../../FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import { useEffect, useState } from "react";
import { useContinuousEvaluationUpdateSetting } from "../../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import Modal from "../../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../../Ui/Input/FormInput";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../../Loader/QvipleLoader";
import {
  existingRequiredField,
  requiredField,
} from "../../../../../Validation/functions/checkingRequiredField";

import Switch from "@mui/material/Switch";
import ClsTheorySubjectListDropdown from "../../ClsTheorySubjectListDropdown";
import styleGrade from "../../../../../../MembersTab/staff/Mainbody/DepartmentHead/Exminations/SeatingArrangement/SeatingArrangement.module.css";
const label = { inputProps: { "aria-label": "Switch demo" } };

const AttendanceMarksSetting = ({
  onClose,
  ceid,
  onRefetch,
  menuData,
  clsId,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    attendance_outof: "",
    attendance_toggle: false,
    attendance_subject: "",
    attendance_grade_count: "",
    attendance_grade_marks: [],
  });
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [continuousEvaluationUpdateSetting] =
    useContinuousEvaluationUpdateSetting();

  useEffect(() => {
    if (menuData) {
      let arr = [];

      if (menuData?.attendance_grade_marks?.length > 0) {
        for (let dt of menuData?.attendance_grade_marks) {
          arr.push({
            start_range: dt?.start_range,
            end_range: dt?.end_range,
            grade_marks: dt?.grade_marks,
          });
        }
      }
      setState({
        attendance_outof: menuData?.attendance_outof,
        attendance_toggle: menuData?.attendance_toggle,
        attendance_subject: menuData?.attendance_subject,
        attendance_grade_count: menuData?.attendance_grade_count,
        attendance_grade_marks: arr,
      });
    }
  }, [menuData]);

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: +e.target.value,
    }));
  };
  const onInputRangeCountChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: +e.target.value,
    }));
    let arr = [];
    for (let i = 0; i < +e.target.value; i++) {
      arr.push({
        start_range: 0,
        end_range: 0,
        grade_marks: 0,
      });
    }
    setState((prev) => ({
      ...prev,
      attendance_grade_marks: arr,
    }));
  };

  const onToggle = (e) => {
    setState((prev) => ({
      ...prev,
      attendance_toggle: e.target.checked,
    }));
  };
  const onSubjectSelect = (dt) => {
    setState((prev) => ({
      ...prev,
      attendance_subject: dt,
    }));
  };

  const onRangeInputChange = (e, index, flow) => {
    const inputarr = [...state.attendance_grade_marks];
    for (let inp = 0; inp < inputarr?.length; inp++) {
      if (inp === index) {
        inputarr[inp][flow] = +e.target.value;
      }
    }
    setState((prev) => ({
      ...prev,
      attendance_grade_marks: inputarr,
    }));
  };

  const onUpdate = () => {
    let validation = requiredField(state, [
      "attendance_toggle",
      "attendance_grade_count",
      "attendance_grade_marks",
    ]);
    if (existingRequiredField(validation)) {
      if (ceid) {
        setDisabled((pre) => !pre);
        let arr = [];
        if (state.attendance_grade_marks?.length > 0) {
          for (let ft of state.attendance_grade_marks) {
            if (ft.end_range) {
              arr.push(ft);
            }
          }
        }

        continuousEvaluationUpdateSetting({
          ceid: ceid,
          bodyParameter: {
            ...state,
            attendance_subject: state.attendance_subject?._id ?? null,
            attendance_grade_marks: arr,
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6> {t("attendance_setting")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("attendance_outof")}
              placeholder={t("attendance_outof_placeholder")}
              name={"attendance_outof"}
              value={state.attendance_outof}
              type={"text"}
              onChange={onInputChange}
              errorShow={errorField["attendance_outof"]}
            />
            <ClsTheorySubjectListDropdown
              onSelect={onSubjectSelect}
              errorShow={errorField["subject"]}
              defalutValue={state?.attendance_subject}
              clsId={clsId}
            />

            <div
              style={{
                marginTop: "1rem",
              }}
            >
              <Switch
                {...label}
                checked={state.attendance_toggle}
                onChange={onToggle}
              />
              <span>{t("attendance_enable")}</span>
            </div>

            <FormInput
              label={t("attendance_range_count")}
              placeholder={t("attendance_range_count_placeholder")}
              name={"attendance_grade_count"}
              value={state.attendance_grade_count}
              type={"tel"}
              onChange={onInputRangeCountChange}
              errorShow={errorField["attendance_grade_count"]}
            />

            <section className={styleGrade.grade_system_header}>
              <h6
                style={{
                  width: "75%",
                  borderRight: "0.5px solid #FFFFFF",
                  padding: "1rem",
                }}
              >
                {t("range")}
              </h6>
              <h6
                style={{
                  width: "25%",
                  padding: "1rem",
                }}
              >
                {t("grade")}
              </h6>
            </section>
            {state.attendance_grade_marks?.map((g_range, index) => (
              <section className={styleGrade.grade_system_body} key={index}>
                <h6
                  style={{
                    width: "75%",
                    borderRight: "0.5px solid #121212",
                    padding: "0.7rem",
                  }}
                >
                  <input
                    type={"text"}
                    value={g_range.start_range}
                    placeholder="00"
                    onChange={(e) =>
                      onRangeInputChange(e, index, "start_range")
                    }
                    className={styleGrade.fee_head_amount_input}
                    style={{
                      width: "20%",
                    }}
                  />
                  To{" "}
                  <input
                    type={"text"}
                    value={g_range.end_range}
                    placeholder="00"
                    onChange={(e) => onRangeInputChange(e, index, "end_range")}
                    className={styleGrade.fee_head_amount_input}
                    style={{
                      width: "20%",
                    }}
                  />
                </h6>
                <h6
                  style={{
                    width: "25%",
                    padding: "0.7rem",
                  }}
                >
                  <input
                    type={"text"}
                    value={g_range.grade_marks}
                    placeholder="00"
                    onChange={(e) =>
                      onRangeInputChange(e, index, "grade_marks")
                    }
                    className={styleGrade.fee_head_amount_input}
                  />
                </h6>
              </section>
            ))}
            <CreateButton label={"update"} onAction={onUpdate} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AttendanceMarksSetting;
