import { useTranslation } from "react-i18next";
import { Link, useLocation, useParams } from "react-router-dom";
import style from "../../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import ContinuousExperimentCardMenu from "./ContinuousExperimentCardMenu";
import { useState } from "react";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../../Utils/Functions/localTime";
const ContinuousExperimentCard = ({ experiment, onRefetch }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [menuData, setMenuData] = useState("");

  const onClose = () => {
    setMenuData("");
  };
  const onMenu = (e) => {
    e.preventDefault();
    setMenuData(experiment);
  };
  return (
    <>
      <Link
        to={`/q/${params.username}/member/subject/${params.sname}?a=ctexperiment`}
        state={{
          ...getQuery.state,
          experiment: experiment,
        }}
      >
        <div className={style.sf_card}>
          <div className={style.sf_card_contatiner}>
            <div className={style.sf_card_contatiner_inner}>
              <h6>
                {t("name")}
                {": "}
                {experiment?.name ?? ""}
              </h6>
              <p>
                {t("experiment_type")} {": "}{" "}
                {experiment?.experiment_type ?? ""}
              </p>
              <p>
                {t("date")} {": "}{" "}
                {dayjs(experiment?.date)?.format("DD/MM/YYYY") ?? ""}
              </p>
              <p>
                {t("created_at")} {": "}{" "}
                {dayjs(experiment?.createdAt)?.format("DD/MM/YYYY") ?? ""}
                {", "}
                {localTimeFormat(experiment?.createdAt, "LT")}
              </p>
            </div>
          </div>
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onMenu}
          />
        </div>
      </Link>
      {menuData && (
        <ContinuousExperimentCardMenu
          onClose={onClose}
          onRefetch={onRefetch}
          menuData={menuData}
        />
      )}
    </>
  );
};

export default ContinuousExperimentCard;
