import { useState } from "react";
import style from "../../../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import { useContinuousEvaluationAllExperimentMarks } from "../../../../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import AllExperimentMarksSetting from "./AllExperimentMarksSetting";
import AllExperimentMarksCard from "./AllExperimentMarksCard";
import Loading from "../../../../../Loader/Loading";
import EmptyMessage from "../../../../../Utils/EmptyMessage/EmptyMessage";

const AllExperimentMarks = ({
  ceid,
  experiment_outof,
  experiment_toggle,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [searchCopy, setSearchCopy] = useState("");
  const [step, setStep] = useState("");

  const {
    continuousEvaluationAllExperimentMarks,
    continuousEvaluationAllExperimentMarksLoading,
    continuousEvaluationAllExperimentMarksRefetch,
  } = useContinuousEvaluationAllExperimentMarks({
    data: {
      ceid: ceid,
    },
    skip: !ceid,
  });

  const onSearchEvent = (e) => {
    if (
      !continuousEvaluationAllExperimentMarksLoading &&
      continuousEvaluationAllExperimentMarks?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
    }
  };

  const onCloseStep = () => {
    setStep("");
  };

  const onRefetchApi = () => {
    onRefetch();
    continuousEvaluationAllExperimentMarksRefetch();
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>
            {t("outof_marks")} {": "} {experiment_outof ?? 0}
          </h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  style={{
                    minWidth: "320px",
                  }}
                  onKeyUp={onCallBindSearch}
                  value={searchCopy}
                />
              </div>
            </section>
            <CreateButton
              label="setting"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={() => setStep(1)}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {continuousEvaluationAllExperimentMarks?.map((student) => (
          <AllExperimentMarksCard student={student} key={student?._id} />
        ))}
        {continuousEvaluationAllExperimentMarksLoading ? (
          <Loading />
        ) : searchCopy ? (
          continuousEvaluationAllExperimentMarks?.length === 0 && (
            <EmptyMessage title="No item found related this search." />
          )
        ) : (
          continuousEvaluationAllExperimentMarks?.length === 0 && (
            <EmptyMessage title="No item." />
          )
        )}
      </UserMemberContentWrapper>

      {step === 1 ? (
        <AllExperimentMarksSetting
          onClose={onCloseStep}
          onRefetch={onRefetchApi}
          ceid={ceid}
          menuData={{
            experiment_outof: experiment_outof,
            experiment_toggle: experiment_toggle,
          }}
        />
      ) : null}
    </>
  );
};

export default AllExperimentMarks;
