import { useTranslation } from "react-i18next";
import { Link, useLocation, useParams } from "react-router-dom";
import style from "../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import InternalEvaluationCardMenu from "./InternalEvaluationCardMenu";
import { useState } from "react";
const InternalEvaluationCard = ({ evaluate, onRefetch }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [menuData, setMenuData] = useState("");

  const onClose = () => {
    setMenuData("");
  };
  const onMenu = (e) => {
    e.preventDefault();
    setMenuData(evaluate);
  };
  return (
    <>
      <Link
        to={`/q/${params.username}/member/subject/${params.sname}?a=evaluate`}
        state={{
          ...getQuery.state,
          evaluate: evaluate,
        }}
      >
        <div className={style.sf_card}>
          <div className={style.sf_card_contatiner}>
            <div className={style.sf_card_contatiner_inner}>
              <h6>
                {t("name")}
                {": "}
                {evaluate?.name ?? ""}
              </h6>
              <p>
                {t("out_of")} {": "} {evaluate?.out_of ?? ""}
              </p>
            </div>
          </div>
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onMenu}
          />
        </div>
      </Link>
      {menuData && (
        <InternalEvaluationCardMenu
          onClose={onClose}
          onRefetch={onRefetch}
          menuData={menuData}
        />
      )}
    </>
  );
};

export default InternalEvaluationCard;
